<template>
  <div id="details">
	<div class="submena clearfix">
		<router-link to="/">首页</router-link>
		<span>></span>
		<router-link to="/">{{book.category}}</router-link>
		<span>></span>
		<span>商品详情</span>
	</div>
	<div class="center_con clearfix">
		<div class="main_menu fl"><img :src="book.image"></div>
		<div class="goods_detail_list fr">
			<h3>{{book.name}}</h3>
            <h6>作者：<a @click="searchAuthor">{{book.author}}</a></h6> <h6>出版社：{{book.publisher}}</h6>
			<p>{{book.description}}</p>
			<div class="prize_bar">
				<div class="show_prize fl">￥<em>{{book.price}}</em></div>
				<div class="show_unit fl">
                  <td class="td-num">
                    <div class="product-num">
                      <a href="javascript:;" class="num-reduce num-do fl" @click='countSub'>-</a>
                      <input type="text" class="num-input" v-model="book.count">
                      <a href="javascript:;" class="num-add num-do fr" @click='countAdd'>+</a>
                    </div>
                  </td>
                </div>
			</div>
			<div class="total">总价：<em>￥{{book.price * book.count}}</em></div>
			<div class="operate_btn">
				<button class="buy_btn" @click="orderIt">立即购买</button>
				<button class="add_cart" @click="addtoCart">加入购物车</button>
			</div>
		</div>
	</div>
  </div>
</template>

<script>
import {request} from "@/network/request.js";
export default{
    name: "",
    data() {
        return {
            book: {
                id: '1',
                name: '魔理沙的魔法书',
                description: 'daze',
                price: 39.99,
                sold_count: 999,
                image: '',
                stock_count: 99,
                author: '雾雨魔理沙',
                publisher: '雾雨魔法店',
                category: '魔导书',
                count: 1,
            }
        }
    },
    created() {
        //获取传入的参数
        let _this = this;
        this.book.id = this.$route.query.book_id;
        // console.log(this.book.id);
        this.getTheBook();
    },
    methods:{
        getTheBook() {
          let sendData = new FormData()
          sendData.append('book_id',this.book.id);
          request({
            method: 'get',
            url: '/api/base/book_info/',
            data: sendData
          }).then(res => {
            if (!res.error_num) {
              let thebook = res.fields;
              this.book.name = thebook.name;
              this.book.description = thebook.description;
              this.book.price = thebook.price;
              this.book.sold_count = thebook.sold_count;
              this.book.image = thebook.image;
              this.book.stock_count = thebook.stock_count;
              this.book.author = thebook.author;
              this.book.publisher = thebook.publisher;
              this.book.category = thebook.category;
              //给book添加count属性
              this.$set(this.book,'count', 1);
            } else {
              this.$message({
                type: 'error',
                message: '获取图书信息失败'
              })
            }
          }).catch(err => {
            console.log(err);
            this.$message({
                type: 'error',
                message: '获取图书信息失败'
              })
          })
        },
        //立即下单
        orderIt() {
            let orderlist = [];
            let thisbook = {
              book_id: this.book.id,
              book_name: this.book.name,
              book_price: this.book.price,
              book_img: this.book.image,
              book_author: this.book.author,
              book_count: this.book.count,
            }
            orderlist.push(thisbook);
            this.$router.push({path:'/order', query:{orderlist: JSON.stringify(orderlist)}});
        },
        //加购
        addtoCart() {
          let sendData = new FormData()
          sendData.append('book_id',this.book.id)
          sendData.append('book_count',this.book.count)
          console.log(sendData);

          request({
            method: 'post',
            url: '/api/trade/add_to_shoppingcart/',
            data: sendData
          })
          .then(res=>{
            console.log(res);
            if(!res.error_num)
            {
              this.$message({
                type: 'success',
                message: '加购成功'
              });
            }
            else
            {
              this.$message({
                type: 'error',
                message: '加购失败',
              });
            }
          }).catch(error=>{
            console.log(error);
            this.$message({
              type: 'error',
              message: error.message
            });
          });
        },
        countAdd() {
          this.book.count++;
        },
        countSub() {
          if (this.book.count>1)
            this.book.count--;
        },
        searchAuthor() {
            this.$router.push({name: 'Search', query:{book_author: this.book.author} })
        }
    }
}
</script>

